body {
  display: block;
  margin: auto;
  background-color: #3a3a3a;
  left: 0;
  top: 0;
  max-width: 720px;
}

#preview {
  font-family: "Questrial", sans-serif;
  color: #489197;
  margin-top: 10px;
  width: 100%;
  min-height: 420px;
  max-height: 450px;
  overflow: scroll;
  border: 4px solid #fd8f00;
  border-bottom: none;
  border-radius: 5px;
  background-color: #e3f8fa;
  padding: 2em 3em;
}

#tool-bar {
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
}
.icon {
  min-width: 40px;
  min-height: 40px;
  color: #e3f8fa;
  border: outset;
  border-radius: 3px;
  padding: 3px;
  margin-right: 4px;
  margin-bottom: 15px;
}

.icon:hover {
  background-color: #489197;
  color: #204144;
}

#editor-box {
  left: 0;
  top: 0;
  display: block;
  margin: 0 auto;
  background-color: #535353;
  border: 4px groove #fd8f00;
  border-radius: 5px;
  width: 100%;
  min-height: 450px;
  padding: 20px 30px 40px 30px;
}
textarea {
  font-family: "Roboto Mono", monospace;
  color: #535353;
  display: block;
  padding: 0.3em;
  width: 100%;
  height: 350px;
  box-sizing: border-box;
  border-color: #fd8f00;
  border-width: 3px;
  background-color: #e3f8fa;
  overflow: scroll;
  padding: 1em;
}
.byLine {
  margin-top: 10px;
  font-size: 12px;
  font-family: "Questrial", sans-serif;
  color: #fd8f00;
  float: right;
}
